.sidebar {
    position: fixed;
    width: 250px;
    height: 100vh;
    background-color: aliceblue;
    transition-duration: 500ms;
    transform: translate(-250px);
}

.sidebar.open {
    transition-duration: 500ms;
    transform: translate(0);

}

.background {
    height: 100%;
    width: 100%;
    backdrop-filter: contrast(5%);
    position: absolute;
}

.nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
}

.nav a {
    text-decoration: none;
    font-family: Oxygen;
    font-size: 28px;
    color: black;
}

.nav a:hover {
    text-decoration: underline;
}

.nav a.active {
    color: #0066CC;
}

.close {
    margin-left: 200px;
    background-color: transparent;
    border-color: transparent;
}

.close img {

    width: 40px;
    height: 40px;
}
