.friendMessage {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.friendImageAndText {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    align-self: flex-start;

}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    left: 1175px;
    top: 180px;
    border-radius: 50%;
    background: #00cc22;
}

.friendText {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: #0066cc;
    border-radius: 5px 5px 5px 0;
}

.angle{
    margin-top: 30px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 0 10px 10px;
    border-color: transparent transparent #0066cc transparent;;
}

.friendText:after {

}

.friendName {
    font-family: Montserrat;
    font-size: 18px;
    color: #FFFFFF;
}

.friendMessageText {
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0em;
    color: #FFFFFF;
}

.friendTime {
margin-left: 10px;
}
