.greetingForm {
    display: flex;
    flex-direction: column;
}

.inputAndButtonContainer {
    display: flex;
    flex-direction: row;
}

.error {
    color: red;
}

.input {
    color: rgba(0, 0, 0, 0.5)
}

.input:focus {
}

.errorInput {
    background-color: white;
    border-color: red;
}

.button {
    margin-left: 25px;
    height: 25px;
    width: 50px;
    color: white;
    background-color: #0066CC;
}

.button:focus {
    outline: aqua solid 1px;
}

.button:active {
    background: aqua;
}

.button:disabled {
    color: #005500;
}

.text {
    color: rgba(0, 0, 0, 0.5)
}

