.message {
    width: 900px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
    align-self: flex-end;
}

.imageAndText img {
    width: 48px;
    height: 48px;
    left: 1175px;
    top: 180px;
    border-radius: 50%;
    background: #00cc22;
}

.text {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    background-color: #0066cc;
    border-radius: 5px 5px 0 5px;
}

.angle{
    margin-top: 30px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 10.5px 0 0 10px;
    border-color: transparent transparent transparent #0066cc;
}

.text:after {

}

.name {
    font-family: Montserrat;
    font-size: 18px;
    color: #FFFFFF;
}

.messageText {
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0em;
    color: #FFFFFF;
}

.time {
    margin-right: 10px;
}